.col-1 {
  width: 10%;
}
.col-2 {
  width: 20%;
}
.col-3 {
  width: 30%;
}
.col-4 {
  width: 40%;
}
.col-5 {
  width: 50%;
}
.col-6 {
  width: 60%;
}
.col-7 {
  width: 70%;
}
.col-8 {
  width: 80%;
}
.col-9 {
  width: 90%;
}
.col-10 {
  width: 100%;
}
@keyframes bouncing {
  0% {
    transform: translateY(0px);
  }
  5% {
    transform: translateY(-5px);
  }
  15% {
    transform: translateY(0px);
  }
  20% {
    transform: translateY(-5px);
  }
  30% {
    transform: translateY(0px);
  }
}
@keyframes sliding-background {
  0% {
    background-position: 0;
  }
  100% {
    background-position: 250px;
  }
}
@font-face {
  font-family: ProductSans;
  src: url('./lib/ProductSans.ttf');
}
input,
textarea {
  font-family: ProductSans, serif;
}
body {
  overflow: hidden;
  margin: 0;
  font-family: ProductSans, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
#Application {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: #c0c0c0;
}
.App {
  width: 1280px;
  height: 720px;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: 0 0 20px -5px #00000033;
  display: flex;
  background-color: #fff;
  overflow: hidden;
}
.App-body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}
.App-body-navBar {
  height: 49px;
  width: 100%;
  flex-shrink: 0;
  border-bottom: 1px solid #dedede;
  display: flex;
  overflow: hidden;
}
.App-body-navBar-backButton,
.App-body-navBar-backButton-hidden {
  height: 29px;
  width: 29px;
  margin-left: 15px;
  align-self: center;
  cursor: pointer;
  transition: margin-left 0.3s ease;
  flex-shrink: 0;
}
.App-body-navBar-backButton-hidden,
.App-body-navBar-backButton-hidden-hidden {
  margin-left: -29px;
}
.App-body-navBar-backButton .back-button,
.App-body-navBar-backButton-hidden .back-button {
  transition: all 0.3s ease;
}
.App-body-navBar-backButton:hover .back-button,
.App-body-navBar-backButton-hidden:hover .back-button {
  fill: #999;
}
.App-body-navBar-backButton:active .back-button,
.App-body-navBar-backButton-hidden:active .back-button {
  transform: translateX(-3px);
}
.App-body-navBar-title {
  font-size: 25px;
  width: 100%;
  align-self: center;
  margin-left: 15px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.App-body-navBar-actions {
  flex-shrink: 0;
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.App-body-navBar-actions-addButton {
  height: 30px;
  width: 30px;
  align-self: flex-end;
  margin-right: 10px;
  cursor: pointer;
}
.App-body-page {
  height: 670px;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  border-radius: 0 0 0 5px;
  box-shadow: inset 0 0 15px -3px #00000033;
}
.App-body-page-loadingAnimation {
  width: 500px;
  height: 500px;
  margin: 0 auto;
}
.App-body-page-loadingAnimation-hidden {
  display: none;
}
.App-body-page-loadingAnimation-loading {
  display: flex;
  opacity: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99;
  flex-direction: column;
  justify-content: center;
  transition: opacity 0.3s ease;
}
.App-body-page-loadingAnimation-transparent {
  display: flex;
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99;
  flex-direction: column;
  justify-content: center;
  transition: opacity 0.3s ease;
}
.App-body-page-popup {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #0006;
}
.App-body-page-popup-hidden {
  display: none;
}
.App-body-page-popup-loading {
  display: flex;
  opacity: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99;
  flex-direction: column;
  justify-content: center;
  transition: opacity 0.3s ease;
  background-color: #00000099;
}
.App-body-page-popup-transparent {
  display: flex;
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99;
  flex-direction: column;
  justify-content: center;
  transition: opacity 0.3s ease;
  background-color: #00000099;
}
.App-body-page .Popup {
  width: 500px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
  overflow: hidden;
}
.App-body-page .Popup-head {
  padding: 10px 15px;
  font-size: 20px;
  border-bottom: 1px solid #dedede;
}
.App-body-page .Popup-body {
  max-height: 500px;
}
.App-body-page .Popup-body-heading {
  display: flex;
  border-bottom: 1px solid #dedede;
  font-size: 15px;
}
.App-body-page .Popup-body-heading-column {
  padding: 2px 10px;
}
.App-body-page .Popup-body-rowItem {
  display: flex;
  border-bottom: 1px solid #dedede;
  font-size: 19px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
}
.App-body-page .Popup-body-rowItem:hover {
  background-color: #0001;
  box-shadow: inset 0 0 5px -3px #0002;
}
.App-body-page .Popup-body-rowItem:active {
  background-color: #0002;
  box-shadow: inset 0 0 5px -3px #0004;
}
.App-body-page .Popup-body-rowItem-column {
  padding: 2px 10px;
}
.App-body-page .Popup-body-rowItem-button {
  display: flex;
  flex-direction: column;
  align-self: center;
  text-align: center;
  border-radius: 5px;
  margin-right: 10px;
  font-size: 14px;
  padding: 2px 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
}
.App-body-page .Popup-body-rowItem-button:hover {
  background-color: #0001;
  box-shadow: inset 0 0 5px -3px #0002;
}
.App-body-page .Popup-body-rowItem-button:active {
  background-color: #0002;
  box-shadow: inset 0 0 5px -3px #0004;
}
.App-body-page .Popup-body-row {
  padding: 10px 15px 10px 0px;
  display: flex;
}
.App-body-page .Popup-body-row-name {
  width: 120px;
  font-size: 17px;
  flex-shrink: 0;
  text-align: center;
}
.App-body-page .Popup-body-row-input,
.App-body-page .Popup-body-row-textarea {
  border: none;
  border-bottom: 1px solid #dedede;
  outline: none;
  font-size: 14px;
  transition: border-bottom-color 0.3s ease;
}
.App-body-page .Popup-body-row-input:focus,
.App-body-page .Popup-body-row-textarea:focus {
  border-bottom-color: #afafaf;
}
.App-body-page .Popup-body-row-textarea {
  flex-grow: 1;
  resize: none;
}
.App-body-page .Popup-body-row-textarea::-webkit-scrollbar {
  display: none;
}
.App-body-page .Popup-body-row-textarea-scroll {
  width: 100%;
  display: flex;
  overflow: hidden;
}
.App-body-page .Popup-body-row-accept {
  padding: 5px;
  margin-left: 20px;
  text-align: center;
  font-size: 15px;
  flex-grow: 1;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
}
.App-body-page .Popup-body-row-accept:hover {
  background-color: #0001;
  box-shadow: inset 0 0 5px -3px #0002;
}
.App-body-page .Popup-body-row-accept:active {
  background-color: #0002;
  box-shadow: inset 0 0 5px -3px #0004;
}
.App-body-page .Popup-footer {
  display: flex;
  margin-top: 10px;
  align-items: center;
  line-height: 30px;
}
.App-body-page .Popup-footer-button {
  flex-grow: 1;
  width: 100%;
  padding: 5px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
}
.App-body-page .Popup-footer-button:hover {
  background-color: #0001;
  box-shadow: inset 0 0 5px -3px #0002;
}
.App-body-page .Popup-footer-button:active {
  background-color: #0002;
  box-shadow: inset 0 0 5px -3px #0004;
}
.App-body-page .Popup-rightAlign {
  justify-content: flex-end;
}
.App-menu {
  width: 299px;
  height: 100%;
  flex-shrink: 0;
  border-left: 1px solid #dedede;
}
.Constructor {
  height: 100%;
}
.Constructor-scrollContainer {
  display: flex;
  flex-direction: column;
}
.Constructor-stagesLine {
  display: flex;
  flex-direction: column;
  margin-right: auto;
  min-height: 670px;
  border-right: 1px solid #dedede;
  background-color: #fff;
  overflow: hidden;
}
.Constructor-stagesLine > .ProjectStage:last-child {
  z-index: 0;
}
.Constructor-stagesLine-init > .ProjectStage:last-child {
  margin-top: -51px;
  z-index: 0;
}
.Main {
  height: 100%;
}
.Main-scrollContainer {
  display: flex;
  flex-wrap: wrap;
  padding: 30px;
}
.Main-project {
  width: 258px;
  margin: 20px;
  height: 130px;
  border: 1px solid #dedede;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  user-select: none;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}
.Main-project:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px -10px #00000033;
}
.Main-project:active {
  transform: scale(1.03);
  box-shadow: 0 0 15px -7px #00000033;
}
.Main-project::before {
  content: '';
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 20px;
  background: linear-gradient(0, #fff, #ffffff00);
}
.Main-project-image {
  justify-content: center;
}
.Main-project-image-plus {
  height: 90px;
}
.Main-project-title {
  height: 30px;
  flex-shrink: 0;
  padding: 0 10px;
  line-height: 30px;
  font-size: 18px;
  border-bottom: 1px solid #dedede;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.Main-project-description {
  height: 100%;
  padding: 5px 10px;
  font-size: 14px;
  line-height: 1;
  overflow: auto;
  margin-right: -17px;
}
.ScrollbarsCustom-Track {
  height: 100% !important;
  top: 0 !important;
  border-radius: 0 !important;
  width: 5px !important;
  transition: width 0.1s ease;
}
.ScrollbarsCustom-Track:hover,
.ScrollbarsCustom-Track:active {
  width: 15px !important;
}
.ScrollbarsCustom-Content {
  padding: 0 !important;
}
.ScrollbarsCustom-Thumb {
  border-radius: 0 !important;
}
.ProjectStage {
  width: 500px;
  transition: background-color 0.3s ease, margin-top 0.3s ease;
}
.ProjectStage-init > .ProjectStage-operation:nth-last-child(2) {
  margin-top: -151px;
}
.ProjectStage-init > .ProjectStage-addTemplate {
  margin-top: -91px !important;
}
.ProjectStage > .ProjectStage-operation:nth-last-child(2) {
  z-index: 1;
}
.ProjectStage:hover {
  background-color: #fafafa;
}
.ProjectStage:hover .ProjectStage-addTemplate {
  margin-top: -31px;
}
.ProjectStage:hover .ProjectStage-addTemplate:focus-within {
  margin-top: 0;
  height: 91px;
}
.ProjectStage:hover .ProjectStage-addTemplate:focus-within .ProjectStage-addTemplate-bottomLine {
  opacity: 0;
}
.ProjectStage:hover .ProjectStage-addTemplate-bottomLine {
  opacity: 1;
}
.ProjectStage:hover .ProjectStage-addTemplate:hover {
  margin-top: 0;
  height: 91px;
}
.ProjectStage:hover .ProjectStage-addTemplate:hover .ProjectStage-operation-column-info-input {
  opacity: 1;
}
.ProjectStage:hover .ProjectStage-addTemplate:hover .ProjectStage-operation-button {
  opacity: 1;
}
.ProjectStage:hover .ProjectStage-addTemplate:hover .ProjectStage-addTemplate-bottomLine {
  opacity: 0;
}
.ProjectStage-header {
  height: 50px;
  padding: 0 10px;
  display: flex;
  border-bottom: 1px solid #dedede;
  position: relative;
  background-color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
  z-index: 2;
}
.ProjectStage-header:hover {
  background-color: #fafafa;
}
.ProjectStage-header:active {
  background-color: #f0f0f0;
}
.ProjectStage-header-title {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  justify-content: center;
}
.ProjectStage-header-title-top {
  font-size: 17px;
  height: 20px;
}
.ProjectStage-header-title-bottom {
  line-height: 1;
  font-size: 23px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.ProjectStage-operation {
  display: flex;
  min-height: 105px;
  background-color: #f5f5f5;
  border-bottom: 1px solid #dedede;
  position: relative;
  overflow: hidden;
  border-left: 5px solid #673AB7;
  transition: height 0.3s ease, margin-top 0.3s ease;
  z-index: 2;
}
.ProjectStage-operation-button {
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: #666;
  opacity: 0;
  transition: opacity 0.3s ease, background-color 0.3s ease;
}
.ProjectStage-operation-button:hover {
  background-color: #0001;
  box-shadow: inset 0 0 5px -3px #0002;
}
.ProjectStage-operation-button:active {
  background-color: #0002;
  box-shadow: inset 0 0 5px -3px #0004;
}
.ProjectStage-operation-expanded {
  height: 100%;
}
.ProjectStage-operation-column {
  display: flex;
  flex-direction: column;
}
.ProjectStage-operation-column-info {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}
.ProjectStage-operation-column-info-input {
  display: inline-flex;
  flex-direction: column;
  overflow: hidden;
  width: 350px;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.ProjectStage-operation-column-interactive {
  width: 200px;
  flex-shrink: 0;
}
.ProjectStage-operation-column-interactive-input {
  width: 200px;
  flex-shrink: 0;
}
.ProjectStage-operation-title {
  padding: 0 10px;
  font-size: 20px;
  line-height: 30px;
  flex-shrink: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.ProjectStage-operation-title-input {
  margin: 5px;
  border: transparent;
  background-color: transparent;
  border-bottom: 1px solid #969696;
  outline: none;
  height: 20px;
  font-size: 20px;
  padding: 0 5px;
}
.ProjectStage-operation-description-scrollContainer {
  overflow-y: auto;
  overflow-x: hidden;
  margin-right: -17px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.ProjectStage-operation-description-scrollContainer-input {
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
}
.ProjectStage-operation-description-content {
  padding: 5px 10px 10px 10px;
  font-size: 14px;
  color: #666;
}
.ProjectStage-operation-description-content-input {
  border: transparent;
  background-color: transparent;
  outline: 0;
  padding: 0 5px;
  height: 50px;
  flex-grow: 1;
  margin: 5px;
  resize: none;
}
.ProjectStage-operation-description-content-input::-webkit-scrollbar {
  display: none;
}
.ProjectStage-operation-bottomInnerShadow {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  background: linear-gradient(#f5f5f500, #f5f5f5 80%);
}
.ProjectStage-operation-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.ProjectStage-operation-container-activeArea {
  min-height: 35px;
  text-align: right;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
}
.ProjectStage-operation-container-activeArea:hover {
  background-color: #0001;
  box-shadow: inset 0 0 5px -3px #0002;
}
.ProjectStage-operation-container-activeArea:active {
  background-color: #0002;
  box-shadow: inset 0 0 5px -3px #0004;
}
.ProjectStage-addTemplate {
  background-color: #eaeaea;
  margin-top: -51px;
  height: 51px;
  transition: margin-top 0.3s ease, height 0.3s ease;
  position: relative;
  display: flex;
}
.ProjectStage-addTemplate-bottomLine {
  position: absolute;
  height: 4px;
  width: 200px;
  background-color: #b5b5b5;
  bottom: 5px;
  left: 150px;
  border-radius: 2px;
  transition: opacity 0.3s ease;
  animation: bouncing 3s infinite ease;
  opacity: 0;
}
.ProjectStage-addTemplate:focus-within {
  margin-top: 0;
  height: 91px;
}
.ProjectStage-addTemplate:focus-within .ProjectStage-operation-column-info-input {
  opacity: 1;
}
.ProjectStage-addTemplate:focus-within .ProjectStage-operation-button {
  opacity: 1;
}
.Operation {
  height: 100%;
  margin: 3px 0;
}
.Operation-EmptyData {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 10px;
}
.Operation-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0 10px;
  text-align: right;
}
.Operation-container-type {
  line-height: 12px;
  font-size: 12px;
  color: #666;
}
.Operation-container-title {
  line-height: 16px;
  font-size: 16px;
}
.Operation-loading {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 10px;
  height: 100%;
  min-height: 35px;
  background-image: linear-gradient(60deg, transparent 0% 25%, #ffffff80 50%, transparent 75% 100%);
  animation: sliding-background 1s linear infinite;
}
.Popup {
  width: 500px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
}
